
.top-navigation {
  background: #1B376B;
  position: fixed;
  width: 100%;
  z-index: 9999;
}
.top-navigation .title {
    color: white;
    font-weight: bold;
}
.top-navigation .top-bar {
    background: #1B376B;
}
.top-navigation .top-bar ul {
      background: #1B376B;
}
.top-navigation .top-bar ul .menu-text a {
        font-size: 24px;
        padding: 0;
        color: white;
}
.top-navigation .top-bar .top-bar-right {
      padding: 8px;
}
.top-navigation .top-bar .top-bar-right .logout {
        padding: 0.7rem 1rem;
        font-size: 24px;
        color: white;
}
.top-navigation .top-bar .top-bar-right .dropdown {
        position: relative;
        display: inline-block;
}
.top-navigation .top-bar .top-bar-right .dropdown .dropdown-button {
          color: white;
          padding: 0.7rem 1rem;
          border: none;
          cursor: pointer;
          display: inline-block;
}
.top-navigation .top-bar .top-bar-right .dropdown .dropdown-button:after {
            border-color: white transparent transparent;
}
.top-navigation .top-bar .top-bar-right .dropdown .dropdown-content {
          display: none;
          position: absolute;
          background-color: #f9f9f9;
          min-width: 160px;
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
          z-index: 1;
          margin: 0;
          padding: 0;
          list-style: none;
}
.top-navigation .top-bar .top-bar-right .dropdown .dropdown-content a {
            color: black;
            padding: 12px 16px;
            text-decoration: none;
            display: block;
}
.top-navigation .top-bar .top-bar-right .dropdown .dropdown-content a:hover {
              background-color: #f1f1f1;
}
.top-navigation .top-bar .top-bar-right .dropdown:hover .dropdown-content {
          display: block;
}
.top-navigation .top-bar .top-bar-right .dropdown:hover .dropdown-button {
          background-color: #365590;
}
.spacer {
  padding-top: 100px;
}



