
.form-selector {
  width: 100%;
  height: 100%;
  min-height: 200vh;
  /*background: #072FB8;*/
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
}
.container.picker {
  width: 100%;
  max-width: 768px;
  margin-left: auto;
  margin-right: auto;
  background: #ffffff;
  padding: 22px 0;
  min-height: 120vh;
  z-index: 1;
}
.header img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 22px;
}
.header h2 {
  text-align: center;
  font-weight: 800;
  text-transform: uppercase;
  color: #C31B36;
  /*font-size: 29px;*/
  margin-bottom: 30px;
}
h3 {
  font-weight: 800;
  text-transform: uppercase;
  color: #B42F3B;
  text-align: left;
  margin: 30px 0;
}
ul.menu.vertical {
  background: #F1F1F1;
  font-size: 24px;
  padding: 20px 0;
}
ul.menu.vertical .heading {
    color: black;
    border-bottom: 1px solid black;
    display: block;
    margin-bottom: 15px;
}
ul.menu.vertical li {
  margin-bottom: 25px;
}
ul.menu.vertical li:last-child {
  margin-bottom: 0;
}
ul.menu.vertical li a {
  color: #C31B36;
  text-decoration: underline;
}
ul.menu.vertical.guidelines {
  padding: 0;
  background: transparent;
}
ul.menu.vertical.guidelines li {
    display: inline-block;
    text-align: left;
    margin: 0;
}
ul.menu.vertical.guidelines li a {
      padding: 0;
      margin: 0 30px 30px 0;
      text-decoration: none;
}
ul.menu.vertical.guidelines li a span {
        color: black;
        font-size: 16px;
        display: inline-block;
        border: 0;
        margin-top: 10px;
        font-weight: bold;
}
ul.menu.vertical.guidelines li a img {
        width: 150px;
        display: block;
}
.menu.vertical.Forms li.action {
  padding: 0 1rem;
}
.menu.vertical.Forms li.action a {
    display: inline-block;
    color: white;
    text-decoration: none;
}



